* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
.container {
  width: 300px;
  height: 100vh;
  background: var(--headerColor);
  position: relative;
  margin: auto;
  overflow: hidden;
  @media only screen and (max-width: 430px) {
    width: 100%;
  }
}
